.login {
    box-sizing: border-box;
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.login_container {
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
    box-shadow: 1px 1px 2px 1px grey;
    border-radius: 20px;

}


.login_container>h1 {
    font-weight: 500;
    margin-bottom: 20px;
}


.login_container>form>h5 {
    margin-bottom: 5px;
}

.login_container>form>input {

    height: 30px;
    width: 98%;
    margin-bottom: 5px;
    background-color: rgb(240, 248, 255);
    border: none;
    padding-left: 5px;
}




.login_container>p {
    margin-top: 15px;
    font-size: 12px;
}


#logo {
    box-shadow: none;
    width: 70px;
}

.login_logo {
    border-radius: 10px;
    width: 130px;
    object-fit: contain;
    /*to setup the best quality of pic at that size*/
    margin: 10px 0px;
}



.login_signinbtn {
    cursor: pointer;
    background-color: #9e7733e6;
    border: none;
    width: 100%;
    padding: 5px;
    height: 30px;
    margin-top: 10px;
    color: white;
    border-radius: 30px;

}


.login_regbtn {
    padding: 5px;
    cursor: pointer;
}